@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,400;0,700;1,200;1,400;1,700&display=swap');

* {
  margin: 0;
  padding: 0;
}

body {
  font-size: 14px;
  background-color: #121020;
  font-family: 'Poppins', sans-serif;
  font-weight: 400; // 200, 400, 700
}

::selection {
  background-color: #EA456E;
  color: #fff;
}

.container {
  width: 1200px;
  margin: auto;
}

header {
  padding: 60px 0;
  display: flex;
  justify-content: flex-end;
}

.arrow-tilt {
  transform: rotate(-45deg);
  position: relative;
  top: 2px;
  margin-left: 3px;
}

nav {
  
  ul {
    float: right;
    display: flex;
    
    li {
      text-transform: uppercase;
      list-style: none;
      color: #fff;
      margin-right: 30px;
      font-weight: 400;

      a {
        text-decoration: none;
        color: #fff;
        transition: .3s;
        cursor: pointer;
        padding: 15px;
        letter-spacing: 0.5px;
        
        &.active {
          color: #EA456E;
          font-weight: 700;
        }
      
        &:hover {
          color: #EA456E;
        }
      }

      &:last-child {
        margin-right: 0px;
      }
    }
  }
}

.hero-section {
  display: flex;
  margin-top: 100px;
  
  .content {
     width: 660px;
     color: #fff;

     .line-wrap {
      overflow: hidden;
      height: 85px;
     }

     h1 {
      font-size: 56px;
      font-weight: 700;
    }

    .paragraph-content {
      font-size: 36px;
      font-weight: 200;
      line-height: 61px;
      margin-bottom: 20px;

      .line-wrap-p {
        overflow: hidden;
        height: 60px;

        span {
          display: block;
          position: relative;
        }
      }
    }

    .line-wrap-link {
      overflow: hidden;
      height: 57px;
    }

    a {
      text-decoration: none;
      display: block;
      color: #EA456E;
      font-size: 18px;
      padding: 15px 0;
      transition: 0.3s;

      &:hover {
        color: #CA0A3A;

        .icon {
          left: 10px;
        }
      }

      .icon {
        position: relative;
        left: 4px;
        top: 3px;
        width: 20px;
        height: 20px;
        display: inline-block;
        overflow: hidden;
        transition: 0.3s;
      }
    }
  }

  .image-container {
    display: flex;
    flex-grow: 1;
    justify-content: center;
    position: relative;
    
    .image {
      width: 372px;
      height: 372px;
      border-radius: 50%;
      background-color: #fff;
      position: relative;

      img {
        width: 100%;
        height: 100%;
        border-radius: 50%;
      }
      
      &:before {
        content: '';
        position: absolute;
        width: 332px;
        height: 332px;
        left: 10px;
        top: 10px;
        background-color: #FF0073;
        border-radius: 100%;
        box-shadow: 27px 25px 37px 15px #ff0073;
        z-index: -1;
      }
    }
  }
}

@keyframes glow {
  from {
    box-shadow: 0 0 10px 2px  #FF0073;
  }
  to {
    box-shadow: 0 0 70px 5px  #FF0073;
  }
}

.brands-container {
  display: flex;
  justify-content: center;
  margin-top: 165px;

  .brands {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  h3 {
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: #5F5D68;
    margin-bottom: 50px;
  }

  ul.logos {
    display: flex;
    list-style: none;
    
    li {
      margin-right: 42px;

      &:last-child {
        margin-right: 0;
      }
    }
  }
}

.work-container {
  margin-top: 165px;

  h3 {
    font-size: 56px;
    font-weight: 700;
    color: #fff;
    margin-bottom: 64px;
  }

  ul {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    gap: 8%;

    li {

      a {
        color: inherit;
        display: block;
        transition: 0.3s;

        * {
          transition: 0.3s;
        }

        &:hover {
          transform: scale(1.05);
          
          .image img {
            // transform: scale(1.05);
          }

          span.link {
            color: #CA0A3A;
          }

          .icon {
            left: 10px;
          }
        }
      }

      width: 46%;
      display: block;
      // padding-right: 20px;
      box-sizing: border-box;
      margin-bottom: 65px;
    }

    h4 {
      font-size: 36px;
      font-weight: 700;
      color: #fff;
      margin-top: 14px;
    } 

    .image {
      // height: 500px;
      width: 100%;
      // background-color: #333;

      img { 
        width: 100%;
      }
    }

    p {
      color: #fff;
      font-size: 18px;
    }

    span.link {
      text-decoration: none;
      display: inline-block;
      color: #EA456E;
      font-size: 18px;
      padding: 15px 0;

      .icon {
        position: relative;
        left: 4px;
        top: 3px;
        width: 20px;
        height: 20px;
        display: inline-block;
        overflow: hidden;
      }
    }
  }
}

.testimonial-container {
  margin-top: 100px;
  padding: 100px 0;
  background-color: #474161;

  h3 {
    font-size: 56px;
    font-weight: 700;
    color: #fff;
    margin-bottom: 64px;
  }

  ul.testimonials {
    list-style: none;
    width: 1300px;
    margin: auto;
    display: flex;
    gap: 2%;
    
    li {
      width: 31.33%;
      display: block;
      background-color: #585370;

      .content {
        height: 350px;
        padding: 50px;
        color: #fff;
        font-size: 24px;
        display: flex;
        // align-items: center;
        justify-content: center;
        text-align: center;
      }

      .person {
        font-size: 18px;
        color: #fff;

        .info {
          align-items: center;
          justify-content: center;
          padding-bottom: 50px;
        }

        .image {
          width: 105px;
          // height: 90px;
          // background-color: #fff;
          margin: auto;
          margin-bottom: 10px;

          img {
            width: 100%;
          }
        }

        .info > p {
          text-align: center;
          // display: inline-block;
        }

        .name {
          font-weight: 700;
        }

        .position {
          color: #ABA9B7;
        }
      }
    }
  }
}

.footer {
  background-color: #000;
  color: #fff;
  padding: 125px 0;

  p {
    // margin-bottom: 10px;
    font-size: 18px;
  }

  h1 {
    font-size: 56px;
    font-weight: 700;
    margin: 0;
    padding: 0;
    // margin-bottom: 10px;
  }
}

.about-section {

  a.resume-link {
      text-decoration: none;
      display: inline-block;
      color: #EA456E;
      font-size: 18px;
      font-weight: 400;
      padding: 15px 0;
      transition: 0.3s;
  
      .icon {
        position: relative;
        left: 4px;
        top: 3px;
        width: 20px;
        height: 20px;
        display: inline-block;
        overflow: hidden;
        transition: 0.3s;
      }

      &:hover {
        color: #CA0A3A;

        .icon {
          left: 10px;
        }
      }
  }
  
  color: #fff;
  font-size: 18px;
  padding-bottom: 100px;

  .hero-section {
    .content {
      width: 100%;
    }
  }

  .about-content {
    display: flex;

    .image {
      width: 500px;

      img {
        width: 500px;
      }
    }

    margin-top: 64px;
    display: flex;
    gap: 40px;
    font-size: 24px;
    font-weight: 200;
    line-height: 40px;

    a {
      color: #EA456E;
      font-weight: 700;
      font-size: 24px;
      transition: 0.3s;
